                            },
                        ]
                        this.bodyType = arr.reduce(function (prev, current) {
                            return prev.score > current.score ? prev : current
                        }, {}).type
                    }
                }

            }


        },
        //上一组
        pre() {
            this.active -= 1
            this.pageList = this.formList.slice(this.active * 7, this.active * 7 + 7)
        },
        //重新测试
        again() {
            this.active = 0
            this.isFinish = false

            this.formList.forEach(item => {
                item.value = ''
            })
            console.log(this.formList);
            this.pageList = this.formList.slice(0, 7)
        },
        //获取总数
        getTotal(arr) {
            console.log(arr);
            let sum = arr.reduce((a, { value }) => a + value, 0)
            return sum
        },
        //获取原始分
        getScore(val) {
            return ((val - 7) / 28) * 100
        }
    },
}
</script>
